}

.page {
  font-optical-sizing: auto;
  scroll-behavior: smooth;
  font-style: normal;
  overflow-x: hidden;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  box-sizing: border-box;
  background-color: #f8f8f8;
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 2;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(38%);
}

@media (width >= 640px) {
  .page__menu:target {
    transform: translateX(230%);
  }
}

